import React from 'react';
import { Group, Line, Rect, Text, Circle } from 'react-konva';
import Room from './Room';
import Sensor from './Sensor';
import Wall from './Wall';
import Door from './Door';
import Stair from './Stair';

const Floor = ({ floor, onSensorSelect, viewMode = "normal" }) => {
  //  floor  ,   
  if (!floor) {
    return <Group />;
  }

  //         
  const getRoomColor = (type) => {
    switch (type) {
      case 'classroom': return '#E0F7FA'; // -   
      case 'hall': return '#F5F5F5'; // -  
      case 'lab': return '#E0F2F1'; // --  
      case 'library': return '#E8F5E9'; // -  
      case 'cafeteria': return '#FFF8E1'; // -  
      case 'gym': return '#F3E5F5'; // -  
      case 'admin': return '#FFEBEE'; // -  . 
      case 'teachers': return '#FFF3E0'; // -  
      case 'wc': return '#E0E0E0'; //   
      case 'medical': return '#F9FBE7'; // -  
      case 'security': return '#ECEFF1'; // --  
      case 'stairs': return '#CFD8DC'; // -  
      case 'storage': return '#EEEEEE'; //   
      case 'computer': return '#E3F2FD'; // -   
      case 'server': return '#E8EAF6'; // -  
      case 'technical': return '#FAFAFA'; //    . 
      case 'assembly': return '#FCE4EC'; // -   
      default: return '#FFFFFF'; //   
    }
  };

  //    
  const rooms = floor.rooms || [];
  const walls = floor.walls || [];
  const doors = floor.doors || [];
  const stairs = floor.stairs || [];
  const sensors = floor.sensors || [];

  return (
    <Group>
      {/*   */}
      {rooms.map(room => (
        <Room
          key={room.id || `room-${Math.random()}`}
          room={room}
          color={getRoomColor(room.type)}
        />
      ))}

      {/*   */}
      {walls.map(wall => (
        <Wall
          key={wall.id || `wall-${Math.random()}`}
          wall={wall}
        />
      ))}

      {/*   */}
      {doors.map(door => (
        <Door
          key={door.id || `door-${Math.random()}`}
          door={door}
        />
      ))}

      {/*   */}
      {stairs.map(stair => (
        <Stair
          key={stair.id || `stair-${Math.random()}`}
          stair={stair}
        />
      ))}

      {/*   */}
      {sensors.map(sensor => (
        <Sensor
          key={sensor.id || `sensor-${Math.random()}`}
          sensor={sensor}
          onClick={() => onSensorSelect && onSensorSelect(sensor)}
          viewMode="normal"
        />
      ))}
    </Group>
  );
};

export default Floor; 