import React from 'react';
import { Group, Circle, Text, Line, Arc, Wedge } from 'react-konva';

const Sensor = ({ sensor, onClick, viewMode = "normal" }) => {
  //  sensor  ,   
  if (!sensor || !sensor.type) {
    return <Group />;
  }

  //       
  const getStatusColor = (status) => {
    switch (status) {
      case 'normal':
        return '#44CC44'; //    
      case 'warning':
        return '#FFAA00'; //   
      case 'alarm':
        return '#FF0000'; //   
      case 'high':
        return '#9900FF'; //    
      default:
        return '#44CC44';
    }
  };

  //       
  const getSensorIcon = (type) => {
    switch (type) {
      case 'temperature':
        return '';
      case 'humidity':
        return '';
      case 'fire':
        return '';
      case 'movement':
        return '';
      case 'security':
        return '';
      case 'server':
        return '';
      case 'water':
        return '';
      case 'gas':
        return '';
      case 'power':
        return '';
      default:
        return '';
    }
  };

  //        
  const sensorRadius = viewMode === "sensors" ? 12 : 10;
  const showLabel = viewMode !== "heatmap";
  const showIcon = viewMode !== "heatmap";
  
  //        
  const statusColor = getStatusColor(sensor.status || 'normal');
  const fillOpacity = viewMode === "sensors" ? 1 : 0.8;
  
  //     
  if (sensor.type === 'security') {
    const cameraAngle = sensor.angle || 0;
    const coneLength = 30 / (viewMode === "sensors" ? 0.8 : 1);

    return (
      <Group onClick={onClick ? () => onClick(sensor) : undefined}>
        {/*   */}
        <Circle
          x={sensor.x}
          y={sensor.y}
          radius={8}
          fill={statusColor}
          opacity={fillOpacity}
          stroke="#333333"
          strokeWidth={1}
        />
        
        {/*   */}
        <Wedge
          x={sensor.x}
          y={sensor.y}
          radius={coneLength}
          angle={60}
          rotation={cameraAngle}
          fill={statusColor}
          opacity={0.2}
          stroke={statusColor}
          strokeWidth={1}
        />
        
        {/*   */}
        {showLabel && (
          <Text
            x={sensor.x - 15}
            y={sensor.y + 10}
            text={sensor.name}
            fontSize={8}
            fontFamily="Arial"
            fill="#333333"
            align="center"
            width={30}
          />
        )}
      </Group>
    );
  }

  //      
  const isAlarming = sensor.status === 'alarm';
  const pulseClass = isAlarming ? 'sensor-alarm' : '';

  return (
    <Group 
      onClick={onClick ? () => onClick(sensor) : undefined}
      className={pulseClass}
    >
      <Circle
        x={sensor.x}
        y={sensor.y}
        radius={sensorRadius}
        fill={statusColor}
        opacity={fillOpacity}
        stroke="#333333"
        strokeWidth={1}
      />
      
      {showIcon && (
        <Text
          x={sensor.x - 5}
          y={sensor.y - 5}
          text={getSensorIcon(sensor.type)}
          fontSize={10}
          align="center"
          verticalAlign="middle"
        />
      )}
      
      {showLabel && (
        <Text
          x={sensor.x - 15}
          y={sensor.y + 12}
          text={sensor.name}
          fontSize={8}
          fontFamily="Arial"
          fill="#333333"
          align="center"
          width={30}
        />
      )}
    </Group>
  );
};

export default Sensor; 